import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SocialApiService } from '../social-api.service';

@Component({
  selector: 'app-welcomescreen',
  templateUrl: './welcomescreen.component.html',
  styleUrls: ['./welcomescreen.component.css']
})
export class WelcomescreenComponent implements OnInit {

  url: string;
  workflowId: string;
  formId: string;

  constructor(private socialService: SocialApiService, private _Activatedroute:ActivatedRoute) { 
    this.workflowId=this._Activatedroute.snapshot.paramMap.get("workflowId");
    this.formId=this._Activatedroute.snapshot.paramMap.get("formId");
    this.url = "/guidedform/done/"+this.formId+"/workflow/"+this.workflowId;
    this.socialService.setUrl(this.url);
  }

  ngOnInit(): void {
    this.url = this.socialService.getUrl();
  }

  clickEvent(): void {
    window.location.href = this.url;
  }
}
