import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WelcomescreenComponent } from "./welcomescreen/welcomescreen.component";
import { ListscreenComponent } from "./listscreen/listscreen.component";
import { ListdetailsscreenComponent } from "./listdetailsscreen/listdetailsscreen.component";

const routes: Routes = [
  { path: 'form/:formId/workflow/:workflowId', component: WelcomescreenComponent },
  { path: 'lists', component: ListscreenComponent },
  { path: 'list', component: ListdetailsscreenComponent },
  { path: '', component: WelcomescreenComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
